export class Model {
  user;
  items;

  constructor() {
    this.user = `Аnonymous`;
    this.items = [
      new TodoItem(0, `Купить биткоины у цыган`, false),
      new TodoItem(1, `Познать дзен и Angular`, false),
      new TodoItem(2, `Украсть лошадь`, false),
      new TodoItem(3, `Поменять струны`, false)
    ]
  }
}

export class TodoItem {
  id;
  action;
  done;

  constructor(id, action, done) {
    this.id = id;
    this.action = action;
    this.done = done;
  }
}


