import { Component } from '@angular/core';
import { Model, TodoItem } from './model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})

export class AppComponent {
  model = new Model;
  todoItems = this.model.items;
  shownTodoForm = false;

  getName() {
    return this.model.user;
  }

  toggleTodoForm() {
    this.shownTodoForm = !this.shownTodoForm;
  }

  delTodoItem(todoItem1) {
    let newTodoItems = this.todoItems.filter(item => item.id != todoItem1.id);
    this.todoItems = newTodoItems;
  }

  addNewTodo(todoForm) {
    if (todoForm.value !== ``) {
      this.todoItems.push(new TodoItem(this.generateRandomId(), todoForm.value, false));
      todoForm.value = ``;
    }
  }

  generateRandomId() {
    return Math.floor((1 + Math.random()) * 0x10000);
  }
}



